import {api} from '../../../api/api.js'
import Item from './InfoItem.vue'
import MaskTwo from './InfoMaskTwo.vue'
export default {
	name:"InfoMask",
	props:{
		item:{},
		index:0
	},
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			show:false,
			list:[],
			typeid:0,
			allList:[]
			
		}
	},
	computed: {
		type() {
			if(this.index === 1) {
				return 0;
			}else if(this.index === 2) {
				return 1;
			}else if(this.index === 3) {
				return 2;
			}else if(this.index === 4) {
				return 1;
			}
		},
		coinType() {
			if(this.index === 1) {
				return 'usdt';
			}else if(this.index === 2) {
				return 'xev';
			}else if(this.index === 3) {
				return 'xv';
			}else if(this.index === 4) {
				return 'xev';
			}
		}
	},
	created() {
		
	},
	methods: {
		getRecord() {
			uni.request({
				url:api.caiwu,
				data: {
					uid:this.uid,
					session_id:this.session_id,
					typeid: this.typeid+1,
					page: 1,
					xev_type: this.coinType,
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res);
					if(res.data.code === "success") {
						this.allList = res.data.list;
					}else  {
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
					}
				}
			})
		},
		tellSuper() {
			this.$emit("tellSuper",'off')
		},
		isShow() {
			if(this.index === 1) {
				this.list = ['兑换','转出','转入','取消'];
			}else {
				this.list = ['兑换','转出','转入','提现','合约收益','邀请收益','团队收益','节点收益','取消']
			}
			this.show = !this.show;
		},
		close(val) {
			this.show = !this.show;
			this.allList = [];
			this.typeid = val;
			if( val === undefined) {
				return;
			}
			this.getRecord();
		}
	},
	components:{
		Item,
		MaskTwo
	}
}
